<template>
  <div class="home" @wheel.stop.prevent="handleWheel">
      <div class="wheel" v-show="num == 1" ref="wheel">
      <div class="wheel page1">
        <el-row :gutter="20" style="width: 50%;margin:0 auto;height:50%;position: absolute;left: 50%;top: 50%;transform: translate(-50%,-50%);">
          <el-col :span="12" v-for="(item, index) in pageData" :key="item.id" style="height:50%;">
            <el-card @click.native="clickCard(index)" style="height: 90%;" :class="'page'+(index+1)"></el-card>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="wheel page2" v-show="num == 2" >456</div>
    <div class="wheel page3" v-show="num == 3">789</div>
    <div class="wheel page4" v-show="num == 4">159</div>
    <el-button v-show="num != 1" type="primary" class="black" round @click="blackHome">返回首页</el-button>
  </div>
</template>

<script>

export default {
  name: 'HomeView',
  data() {
    return {
      lastTime: null,
      num: 1,
      maxNum: 4,
      offsHeight: 0,
      pageData: [{title: '第一页',id: '1'},{title: '第二页',id: '2'},{title: '第三页',id: '3'},{title: '第四页',id: '4'}]
    }
  },
  components: {
  },
  created() {
    this.lastTime = new Date()
  },
  mounted() {
    this.offsHeight = this.$refs.wheel.offsetHeight
  },
  methods: {
    throttle (fn, gapTime) {
      let _this = this
      return function () {
        let _nowTime = +new Date()
        if (_nowTime - _this.lastTime > gapTime || !_this.lastTime) {
          fn(...arguments)
          _this.lastTime = _nowTime
        }
      }
    },
    handleWheel(e) {
      // e.stopPropagation()
      this.throttle(this.pageUpOrDown,60)(e)
    },
    pageUpOrDown (e) {
      if (e.deltaY > 0) {
        this.num ++
        if (this.num > this.maxNum) {
          this.num = 1
        }
        window.scrollTo(0,this.offsHeight*this.num)
      } else if (e.deltaY < 0) {
        this.num --
        if (this.num < 1) {
          this.num = this.maxNum
        }
        window.scrollTo(0,this.offsHeight*this.num)
      }
    },
    clickCard(index) {
      this.num = index + 1
      window.scrollTo(0,this.offsHeight*this.num)
    },
    blackHome() {
      this.num = 1
      window.scrollTo(0,this.offsHeight*this.num)
    }
  }
}
</script>

